.gunpool {
  :global {
    .ant-tabs-large > .ant-tabs-nav .ant-tabs-tab {
      font-size: 20px;
    }
  }
  .group {
    display: flex;
    align-items: center;
    div {
      color: #0099cc;
      margin: 0 10px;
      cursor: pointer;
    }
  }
 
}
.amount {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  div {
    font-size: 16px;
    margin-top: 10px;
    cursor: pointer;
  }
  .errorTip {
    font-size: 14px;
    width: 100%;
    color: #f00;
    margin-top: 5px;
  }
}