body {
  margin: 0;
  background-color: #fff;
  // font-family: 'PingFang Regular';
}

ul,
li,
p,
h3 {
  margin: 0;
  padding: 0;
}
div {
  box-sizing: border-box;
}
li {
  list-style: none;
}

.main {
  width: 100%;
  min-height: 100vh;
  font-size: 14px;
  background: #f6f6f6;
  box-sizing: border-box;
  .mainContent {
    width: 80%;
    margin: 20px auto 0;
  }
}



:global {
  .am-toast.am-toast-mask {
    background: #00000073;
  }
  .am-toast-notice-content {
    .am-toast-text {
      display: flex;
      font-size: 14px;
      align-items: center;
      .am-toast-text-info {
        margin: 0 0 0 5px !important;
      }
    }
  } 
}

/*css主要部分的样式*/
::-webkit-scrollbar {
  width: 5px;
  /*对垂直流动条有效*/
  height: 5px;
  /*对水平流动条有效*/
}

/*定义滚动条的轨道颜色、内阴影及圆角*/
::-webkit-scrollbar-track {
  background-color: transparent;
  border-radius: 2px;
}


/*定义滑块颜色、内阴影及圆角*/
::-webkit-scrollbar-thumb {
  border-radius: 3px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, .3);
  background-color: rgba(255, 255, 255, 0.2);
}

/*定义两端按钮的样式*/
::-webkit-scrollbar-button {
  background-color: transparent;
}

/*定义右下角汇合处的样式*/
::-webkit-scrollbar-corner {
  background: transparent;
}

