.dividendpool {
  :global {
    .ant-tabs-large > .ant-tabs-nav .ant-tabs-tab {
      font-size: 20px;
    }
  }
  .group {
    display: flex;
    align-items: center;
    div {
      color: #0099cc;
      margin: 0 10px;
      cursor: pointer;
    }
  }
  .info {
    display: flex;
    margin-bottom: 30px;
    align-items: center;
    button {
      margin-right: 10px;
    }
    .infoLabel {
      font-size: 18px;
      text-align: right;
    }
    .infoValue {
      font-size: 18px;
    }
  }
}