.headerWrapper {
  width: 100%;
  height: 60px;
  display: flex;
  padding: 12px;
  justify-content: space-between;
  align-items: center;
  background: linear-gradient(90deg, #0090E0 3%, #14C1B2 98%);
  .logo {
    width: 120px;
    color: #fff;
  }
  .menu {
    margin-right: 50px;
    color: #ccc;
    a {
      font-size: 16px;
      display: inline-block;
      color: #000;
      width: 120px;
      margin: 0 20px;
      text-align: center;
      &.active {
        color: #fff;
        font-weight: bold;
      }
    }
  }
  .user {
    font-size: 16px;
    margin-right: 40px;
    max-width: 200px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    .connect {
      color: #fff;
      cursor: pointer;
    }
  }
}