.bridge {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  .info {
    display: flex;
    margin-bottom: 30px;
    align-items: flex-start;
    flex-wrap: wrap;
    .infoLabel {
      font-size: 18px;
      width: 150px;
      text-align: right;
    }
    .infoValue {
      width: 150px;
    }
    .balance {
      width: 100%;
      font-size: 14px;
      margin-top: 8px;
      cursor: pointer;
    }
    .errortip {
      width: 100%;
      font-size: 14px;
      color: #f00;
      margin-top: 8px;
    }
  }
  .history {
    font-size: 18px;
    color: #0099cc;
    cursor: pointer;
    margin: 30px 120px 0 0;
  }
  .group {
    display: flex;
    align-items: center;
    div {
      color: #0099cc;
      margin: 0 10px;
      cursor: pointer;
    }
  }
}